
function ModuleOne({message}){

    return (
        <>
            {message.introduction.map((ms, index) => (
                <p key={`introduction-${index}`}>{ms}</p>
            ))}
            <div key="arrange-description-card-1" className="grid place-items-center my-4">

                <div key="arrange-description-card1-1" className='grid place-items-center h-28 w-20 rounded-[10px] border text-center divination-arrange-description-card'>
                 1
                </div>
            </div>
            {message.meaning.map((ms, index) => (
                <p key={`meaning-${index}`}>{ms}</p>
            ))}

        </>
    )

}

