import { useTranslation } from 'react-i18next';

import TarotSvg from '../../../static/tarot-card.svg'

import { UPRIGHT_REVERSED_SYMBOL } from '../../../Constant'


// TODO single cards

function RuneSmallResultCard({card}){

    switch(card.code) {
        default:
            return (
                <>
                <div className='grid place-content-center mb-2'>
                    <div className='h-28 w-[5rem] grid place-items-center rounded-[20px] shadow-sm divination-rune-result-card drop-shadow-md'><p className="pb-2 text-4xl">{card.photo}</p></div>
                    
                </div>
                <p className='text-center font-bold golden-word break-keep text-sm'>{UPRIGHT_REVERSED_SYMBOL[card.code]} {card.title}</p>
                </>
            )
        
        case "2":
            return (
                <>
                <div className='grid place-content-center mb-2'>
                    <div className='h-28 w-[5rem] grid place-items-center rounded-[20px] shadow-sm divination-rune-result-card drop-shadow-md'><p className="pb-2 text-4xl rotate-180">{card.photo}</p></div>
                    
                </div>
                <p className='text-center font-bold golden-word break-keep text-sm'>{UPRIGHT_REVERSED_SYMBOL[card.code]} {card.title}</p>
                </>
            )


    }

}

function RuneResultCard({card}){

    switch(card.code) {
        default:
            return (
                <>
                <div className='grid place-content-center mb-2'>
                    <div className='h-24 w-[4.5rem] grid place-items-center rounded-[20px] shadow-sm divination-rune-result-card drop-shadow-md'><p className="pb-2 text-4xl">{card.photo}</p></div>
                    
                </div>
                <p className='text-center font-bold golden-word break-keep text-sm'>{UPRIGHT_REVERSED_SYMBOL[card.code]} {card.title}</p>
                </>
            )
        
        case "2":
            return (
                <>
                <div className='grid place-content-center mb-2'>
                    <div className='h-24 w-[4.5rem] grid place-items-center rounded-[20px] shadow-sm divination-rune-result-card drop-shadow-md'><p className="pb-2 text-4xl rotate-180">{card.photo}</p></div>
                    
                </div>
                <p className='text-center font-bold golden-word break-keep text-sm'>{UPRIGHT_REVERSED_SYMBOL[card.code]} {card.title}</p>
                </>
            )


    }

}

function OneSizeResultCard ({card}) {
    return (
        <>
        <div className='grid place-content-center mb-2'>
            <div className='h-28 w-[5rem] rounded-[5px] border-2 divination-card grid place-items-center drop-shadow-md'>
                <div><img src={TarotSvg} alt="Tarot card SVG" className='h-[6.5rem] w-[5rem] pt-1' /></div>
            </div>
            
        </div>
        <p className='text-center font-bold golden-word break-keep text-sm'>{card.positionId && UPRIGHT_REVERSED_SYMBOL[card.positionId] } {card.title}</p>
        </>
    ) 
}

function TwoSizeResultCard ({card}) {
    return (
        <>
        <div className='grid place-content-center mb-2'>
            <div className='h-28 w-[5rem] rounded-[5px] border-2 divination-card grid place-items-center drop-shadow-md'>
            <div><img src={TarotSvg} alt="Tarot card SVG" className='h-[6.5rem] w-[5rem] pt-1' /></div>
            </div>
            
        </div>
        <p className='text-center font-bold golden-word break-keep text-sm'>{card.positionId && UPRIGHT_REVERSED_SYMBOL[card.positionId] } {card.title}</p>
        </>
    ) 
}

function ResultCard ({ card }) {
    return (
        <>
        <div className='grid place-content-center mb-2'>
            <div className='h-24 w-[4.5rem] rounded-[5px] border-2 divination-card grid place-items-center drop-shadow-md'>
            <div><img src={TarotSvg} alt="Tarot card SVG" className='h-[5.5rem] w-[4.5rem] pt-1' /></div>

            </div>
            
        </div>
        <p className='text-center font-bold golden-word break-keep text-sm'>{card.positionId && UPRIGHT_REVERSED_SYMBOL[card.positionId] } {card.title}</p>
        </>
    ) 
}


function MessageCardExplanation(){
    const { t } = useTranslation();
    const messageCardExplanation = t('divination.prediction.initMessage')
    return (
        <>
            {messageCardExplanation}
        </>
    )
}

export { OneSizeResultCard, TwoSizeResultCard, ResultCard, RuneSmallResultCard, RuneResultCard, MessageCardExplanation};