import i18n from "i18next";
import { initReactI18next } from "react-i18next";

import enTranslation from './locales/en.json';
import zhTranslation from './locales/zh.json';

const defaultLanguage = 'en';

const userLanguage = JSON.parse(localStorage.getItem('userLanguage')) || navigator.language;

// the translations
const resources = {
  //  zh-TW  zh-TW en
  [userLanguage === 'zh-TW' ? 'zh-TW' : defaultLanguage]: {
    translation: userLanguage === 'zh-TW' ? zhTranslation : enTranslation
  },
  // 
  // ...
}

// Original
// {
//     en: {
//       translation: enTranslation
//     },
//     zh: {
//       translation: zhTranslation
//     }
//   };

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    lng: userLanguage === 'zh-TW' ? 'zh-TW' : defaultLanguage,
    // keySeparator: false, // we do not use keys in form messages.welcome
    fallbackLng: defaultLanguage, // 
    interpolation: {
      escapeValue: false // react already safes from xss
    }
  });

export default i18n;