function HandleExplanation ({message, method}){

    switch(method) {
        default:
            return (
                <>
                {message.description.map((description, index) => (
                    <h3 key={`${message.id}-${index}`}>{description}</h3>
                ))}
                </>
            )
    
        case "runes":
            return (
                <>
                    <h3 key={`rune-meaning-${message.id}-1`}>{message.meaning[0]}</h3>
                    <h3 key={`rune-meaning-${message.id}-2`}>{message.meaning[1]}</h3>
                    {message.description.map((description, index) => (
                        <React.Fragment key={`rune-description-${message.id}-${index}`}>
                            <h3 key={`rune-title-${message.id}-${index}`} className='text-base font-bold text-center text-gray-300 my-2'>{description.title}</h3>
                            <h3 key={`rune-item-${message.id}-${index}`} className='pb-2'>{description.item}</h3>
                        </React.Fragment>
                    ))}
                </>
            )
        
            case "tarot":
                return (
                    <>
                        <h3 key={`tarot-meaning-${message.id}-1`}>{message.meaning[0]}</h3>
                        {message.description.map((description, index) => (
                            description.item && (
                            <React.Fragment key={`tarot-description-${message.id}-${index}`}>
                                <h3 key={`tarot-title-${message.id}-${index}`} className='text-base font-bold text-center text-gray-300 my-2'>{description.title}</h3>
                                <h3 key={`tarot-item-${message.id}-${index}`} className='pb-2'>{description.item}</h3>
                            </React.Fragment>
                            )
                        ))}
                    </>
                )

        case "lenormand":
                return (
                    <>
                    {message.description.map((description, index) => (
                        <React.Fragment key={`lenormand-description-${message.id}-${index}`}>
                            <h3 key={`lenormand-title-${message.id}-${index}`} className='text-base font-bold text-center text-gray-300 my-2'>{description.title}</h3>
                            <h3 key={`lenormand-item-${message.id}-${index}`} className='pb-2'>{description.item}</h3>
                        </React.Fragment>
                    ))}
                </>
                )


    }

}

function SingleExplanation ({ message, photo, method, closeFunc }){

    const handleClickInside = (e) => {
        e.stopPropagation(); // 
      };
    
      const [isHide, setIsHide] = useState(true);   
      const handleScroll = (e) => {
        // Adjust the scroll threshold according to your needs
        const scrollThreshold = 300;
    
        // Check if the scroll position is greater than the threshold
        if (e.target.scrollTop > scrollThreshold) {
            setIsHide(false);
        } else {
            setIsHide(true);
        }
      };
    // TODO If need carousel in future version.
    
    return (
        <>
        <div className={`absolute h-[38rem] w-[22rem] z-30 border-2 divination-single-card-section rounded-[20px] centered-div`} onClick={handleClickInside} >
            <div className='flex'>
                <div className='flex justify-start grow'>
                    <div className={`${isHide && 'hidden'} golden-word text-xl pt-2 pb-1 pl-6`}>{message.position}{message.title}</div>
                </div>
                <div className='flex justify-end'>
                    <div className='pt-2 pl-3 pr-4 pb-1 cursor-pointer' onClick={closeFunc}>
                        <FontAwesomeIcon icon={faXmark} size="xl" style={{color: "#f8d781",}} />
                    </div>
                </div>
            </div>
            <div className='px-8 pt-1'>
                <div className='overflow-y-auto' onScroll={handleScroll}>
                <div className='px-14 pb-4'>
                    {/* TODO Switch photo */}
                        {photo}
                    {/* <div className='h-72 w-[12rem] rounded-[5px] border-2 divination-card bg-contain bg-center' style={{backgroundImage: `url("${photo}")`}}></div> */}
                </div>
                <p className='golden-word text-xl mb-1'>{message.position}{message.title}</p>
                <div className='h-48 whitespace-pre-line'>
                    <HandleExplanation message={message} method={method} />
                </div>
                </div>
            </div>
        </div>
        </>
    ) 

}

