const getUserLanguage = () => {

    const userLanguage = JSON.parse(localStorage.getItem('userLanguage')) || null;
    if (userLanguage){
        return userLanguage;
    } else {
        const detectedLanguage = navigator.language;
    
        let userNewLanguage;
      
        if (detectedLanguage === 'zh-TW') {
          userNewLanguage = 'zh-TW';
        } else {
          userNewLanguage = 'en';
        }
    
        localStorage.setItem('userLanguage', JSON.stringify(userNewLanguage));
      
        return userNewLanguage; // 
    }
    
  };


  const fetchData = async(body) => {
    const userLanguage = getUserLanguage();

    body.lang = userLanguage
    try {
        const response = await fetch(ROUTE_API + "/api/v2/web/divination", {
          method: "POST",
          body: JSON.stringify(body),
        });
    
        if (!response.ok) {
          // 
          throw new Error('Network response was not ok');
        }
    
        const data = await response.json();
        return data;

      } catch (error) {
        console.error('Error calling API:', error);
        throw error; // 
      }
    };


export { PolyButton, Card, RunesCard, ResultCard, SingleExplanation, fetchData, LoadingPage, getUserLanguage};