
const getRandomValue = (min, max) => {
    return Math.floor(Math.random() * (max - min + 1) + min);
  };


// Introduction

const getFixedDeckValue = (index) => {
    const fixedValues = {
      0: ['0.875rem', 8],
      1: ['0.75rem', 7],
      2: ['0.625rem', 6],
      3: ['0.5rem', 5],
      4: ['0.375rem', 4],
      5: ['0.25rem', 3],
      6: ['0.125rem', 2],
      7: ['0px', 1],
    };
  
    // Return the rotation value for the given index
    return fixedValues[index] || ['1rem', 10];
  };

const ShuffleCardsGenerator = ({ count, isSpread }) => {
    // Generate card positions, translateX, zIndex dynamically
    const generateShuffleCardPositions = () => {
    return Array.from({ length: count }, (_, index) => ({
      position: 'absolute',
      left: getFixedDeckValue(index)[0],
      top: '0px',
      zIndex: getFixedDeckValue(index)[1],
      ...isSpread && {transform: `translate(${getRandomValue(100, 200)}px, ${getRandomValue(100, 200)}px) rotate(${getRandomValue(100, 360)}deg)` },
      ...isSpread && {zIndex: getRandomValue(0, 50) }, 
        }));
    };
    return generateShuffleCardPositions();
};

