// Fixed Cards
const ExpandFixedCardsGenerator = () => {

    const fixedCard = [
        // 
        ['16px', 39], ['14px', 38], ['12px', 37],
        ['10px', 36], ['8px', 35], ['6px', 34], ['4px', 33], 
        ['2px', 32], ['0px', 31]
        ]

    const generateExpandFixedCardPositions = () => {
        return Array.from({ length: 9 }, (_, index) => ({
            position: 'absolute',
            left: fixedCard[index][0],
            top: '0px',
            zIndex: fixedCard[index][1], // Reverse order for zIndex
            }));
        };
        return generateExpandFixedCardPositions();

};


