const getRotationValue = (index) => {
    // Define your rotation values based on the index
    const rotationValues = {
      0: [-13, '12px'],
      1: [-11, '8px'],
      2: [-8, '8px'],
      3: [-6, '4px'],
      4: [-4, '0px'],
      5: [-3, '0px'],
      31: [13, '12px'],
      30: [11, '8px'],
      29: [8, '8px'],
      28: [6, '4px'],
      27: [4, '0px'],
      26: [3, '0px']
    };
  
    // Return the rotation value for the given index
    return rotationValues[index] || [0, '0px'];
  };

  const getFixedValue = (index) => {
    const fixedValues = {
      0: [-13, '12px', '0px'],
      1: [-13, '8px', '2px'],
      2: [-13, '8px', '2px'],
      3: [-11, '4px', '4px'],
      4: [-11, '0px', '4px'],
      5: [-11, '0px', '6px'],
    };
  
    // Return the rotation value for the given index
    return fixedValues[index] || [-9, '0px', '8px'];
  };

  const CardsGenerator = ({ isSpread }) => {
    // Generate card positions, translateX, zIndex dynamically
    const generateCardPositions = () => {
    return Array.from({ length: 32 }, (_, index) => ({
      position: 'absolute',
      left: getFixedValue(index)[2],
      top: getFixedValue(index)[1],
      transform: `rotate(${getFixedValue(index)[0]}deg)`,
      ...isSpread && {top: getRotationValue(index)[1], left: '0%'},
      ...isSpread && {transform: `translateX(${index * 8}px) rotate(${getRotationValue(index)[0]}deg)`},
      zIndex: index + 1, // Reverse order for zIndex
        }));
    };
    return generateCardPositions();
};


export { ShuffleCardsGenerator, ExpandFixedCardsGenerator, CardsGenerator };


