import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleLeft } from '@fortawesome/free-solid-svg-icons'

import CardsService from './divinations/cardService/Cards';
import MoonBlock from './divinations/moonBlockService/MoonBlock';


import { MAX_SCREEN_WIDTH, CARD_STYLE_SETTINGS } from './Constant';


function Header({setRouteFunc}){

    const handleClear = () => {
    
        // Navigate back to homePage
        setRouteFunc("");

        const userPathMonitor = {};
        // Save localStorage
        localStorage.setItem('userPathMonitor', JSON.stringify(userPathMonitor));
      };

    // TODO slide Menu?
    return (
        <div className={`${MAX_SCREEN_WIDTH} mx-auto h-14 border divination-navbar fixed inset-x-0 top-0`}>
        <div className='h-14 w-14' onClick={() => handleClear()}>
            <div className='pl-4 pt-4'>
                <FontAwesomeIcon icon={faAngleLeft} size="xl" style={{color: "#e9d69d",}} />
            </div>
        </div>
        </div>
    )
    }

    
function MainSection({ method }){

    const selectedStyle = CARD_STYLE_SETTINGS[method] || CARD_STYLE_SETTINGS.default;
    
    // Pass Title here
    switch(method) {
        // TODO Runes Components
        case "runes":
            return <CardsService method={method} selectedStyle={selectedStyle} />
        
        case "tarot":
            return <CardsService method={method} selectedStyle={selectedStyle} />
        
        case "lenormand":
            return <CardsService method={method} selectedStyle={selectedStyle} />

        case "poe":
            return <MoonBlock method={method}/>
        
        case "whispers":
            return <CardsService method={method} selectedStyle={selectedStyle} />
        
        case "wisdom":
            return <CardsService method={method} selectedStyle={selectedStyle} />

        default:
            console.log("No Service not found.")
    
    }
}
    
function DivinationRoom({methodValue, setRouteFunc}){

    const [ method, setMethod ] = useState(methodValue)

    useEffect(() => {
        const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
        if (localData){
            if (localData.method){
                setMethod(localData.method)
            }
        }
      }, []);
    
    return (
      <>
      <div className='wallpaper down-wallpaper-color divination-animation'> 
        <Header setRouteFunc={setRouteFunc}/>
        {/* <h1>{location.state.method}</h1> */}
        <MainSection method={method} />
        </div>
      </>
    )
    
    }

export default DivinationRoom;