import React, { useEffect, useState, useRef } from 'react';

import { useTranslation } from 'react-i18next';

import { fetchData, PolyButton } from '../Component';
import { MiriMessages } from '../Miri';

import { ShuffleCardsGenerator } from './Animation'


function Guide({method, changeStage}){
    const { t } = useTranslation();

    const [data, setData] = useState("")
    const [isLoading, setIsLoading] = useState(true);
    const expandRef = useRef(false);

    let body = {
        "method": method,
        "action": "guide",
    }

    useEffect(() => {
        //  API
        const detectData = async () => {
        try{
            const data = await fetchData(body);
            setData(data);
            setIsLoading(false);
            expandRef.current = true;
          } catch (error) {
            console.error(error)
          }
        };
        detectData();

      }, []);

    const message = (
        <React.Fragment>
            <p>{data}</p> 
        </React.Fragment>
        )
    
    const button = (
        <React.Fragment>
                <PolyButton func={() => changeStage("meditation")} title={t('divination.buttons.enter')}/>
    </React.Fragment>
    )

    const loadingMessage = (
        <div className="flex justify-center loading-text mt-10">
        <span className="dot1"></span>
        <span className="dot2"></span>
        <span className="dot3"></span>
    </div>
    )

    return (
        <>
        {isLoading ? (
            <MiriMessages message={loadingMessage} buttons={false} hideEffect={false} />
            // <MiriMessagesLoadingPage />
        ) : (
        <MiriMessages message={message} buttons={button} hideEffect={true} isExpand={expandRef} settings={'introduction'}/>
        )
        }
        
        </>
    )

}

function Introduction({method, setStageFunc, selectedStyle}){
    // TODO Runes 
    const { t } = useTranslation();

    const cardStyle = selectedStyle.cardStyle;

    const [isGuide, setGuide] = useState(false)

    const changeToGuide = () => {
        setGuide(true);
      };

    const message = (
        <React.Fragment>
            <p>{t('divination.introduction.hello')}</p>
            <p>{t('divination.introduction.welcome')}{t(`divination.method.${method}`)}{t('divination.introduction.guide')}</p> 
            <div className='flex justify-center gap-2 pt-4'>
                <PolyButton func={() => changeToGuide()} title={t('divination.buttons.introduce')}/>
                <PolyButton func={() => changeStage("meditation")} title={t('divination.buttons.choose')}/>
            </div>
        </React.Fragment>
        )

    // Card Animation
    const [isSpread, setIsSpread] = useState(false);
    // const [randomCardPositions, setRandomCardPositions] = useState([]);

    const handleAnimate = () => {
        setIsSpread(!isSpread);
      }

    const randomCardPositions = ShuffleCardsGenerator({ count: 32, isSpread });

    useEffect(() => {
        // generateRandomCardPositions(32);
        setTimeout(() => {
            handleAnimate();
          }, 1000);
        
        }, []);

    const changeStage = (stage) => {
        // Navigate to divination
        handleAnimate();
        setTimeout(() => {
            setStageFunc(stage);
          }, 2000);
        // setStageFunc(stage);
      };

    return (
        <>
        <div className='pt-24 px-8'>
            {/* <div className='grid m-auto'> */}
                <div className='relative h-96 w-24'>
                            {randomCardPositions.map((pos, index) => (
                                <div key = {`"div${index}"`} className={`transition-all delay-300 ease-linear duration-500 ${isSpread && 'drop-shadow-xl'}`}
                                    style={pos}>
                                    {cardStyle}</div>
                                ))}
                </div>
            {/* </div> */}
        </div>
        {/* <button className='relative' onClick={handleCloseAnimate}>213</button> */}
        {isGuide ? (
            <Guide method={method} changeStage={changeStage} />

        ) : (
            <MiriMessages message={message} buttons={null} hideEffect={false} />
        )}

        
        </>
    )
}

export default Introduction;