import React, { useState, useEffect, useRef } from 'react';

import { useTranslation } from 'react-i18next';

import { PolyButton } from '../Component';
import { MiriMessages } from '../Miri';
import { faLariSign } from '@fortawesome/free-solid-svg-icons';
import { CardsGenerator } from './Animation';


function ChooseCards({setStageFunc, chosenCardCounts, selectedStyle}){
    // TODO animation
    // 1. Closed cards move to expand.
    // 2. Generate fixed number of cards and it's animation. (this version doesn't allow pass the number of cards based on method.)
    // 3. After choose, enter explanation.
    const { t } = useTranslation();
    const changeStage = () => {
        setStageFunc('explanation');
      };


    const overlay = selectedStyle.overlayStyle;
    const cardStyle = selectedStyle.cardStyle

    const [capableChosenNums, setCapableChosenNums] = useState(chosenCardCounts)

    const checkMessage = (
    <React.Fragment>
        <p>{t('divination.chooseCards.checkMessageYouHave')} {chosenCardCounts} {t('divination.chooseCards.checkMessageSure')}</p>
        <div className='grid justify-items-center pt-4'><PolyButton func={changeStage} title={t('divination.buttons.sure')}/></div>
    </React.Fragment>
    );

    const [selectedCards, setSelectedCards] = useState([]);

    const initMessage = `${t('divination.chooseCards.initMessageChoose')} ${capableChosenNums} ${t('divination.chooseCards.initMessageNum')}`
    const [message, setMessage] = useState(initMessage);

    useEffect(() => {
        if (capableChosenNums !==0){
        setMessage(initMessage);
        } else {
            setMessage(checkMessage);
        }
      }, [capableChosenNums]);

    const handleChooseCard = (cardId) => {
        //  
        if (selectedCards.length < chosenCardCounts) {
          // 
          //setCapableChosenNums(prevCount => prevCount - 1)
          if (!selectedCards.includes(cardId)) {
            // 
            setSelectedCards([...selectedCards, cardId]);
            setCapableChosenNums(prevCount => prevCount - 1)
            // setMessage(initMessage);
          } else {
            // 
            setSelectedCards(selectedCards.filter((id) => id !== cardId));
            setCapableChosenNums(prevCount => prevCount + 1)
            // setMessage(initMessage);
          }
        }
        else {
            if (selectedCards.includes(cardId)){
                setSelectedCards(selectedCards.filter((id) => id !== cardId));
                setCapableChosenNums(prevCount => prevCount + 1)
                // setMessage(initMessage);
            }
            // } else {
            //     setMessage(checkMessage);
            // }
        }
      };

      const handleClickOutside = (event) => {
        if (cardRef.current && !cardRef.current.contains(event.target)) {
            // do nothing.
            if (miriRef.current && miriRef.current.contains(event.target)) {

              } else {
                setSelectedCards([]);
                setCapableChosenNums(chosenCardCounts);
              }
            
        }};

    const [isSpread, setIsSpread] = useState(false);
    const cardRef = useRef(null);
    const miriRef = useRef(null);
    const cardPositions = CardsGenerator({ isSpread });

    const handleAnimate = () => {
      setIsSpread(!isSpread);
    }

    useEffect(() => {
      handleAnimate()
      document.addEventListener('click', handleClickOutside);
      return () => {
        document.removeEventListener('click', handleClickOutside);
      };
    }, []);

    return (
        <>
        <div className='pt-36 px-8'>
            <div ref={cardRef} className='relative'>
              {cardPositions.map((card, index) => (
              <div key={index} onClick={() => handleChooseCard(index)} className="transition-all delay-300 ease-linear duration-500 drop-shadow-lg" style={card}>
                  {selectedCards.includes(index) && <div className="overlay" style={overlay}></div>}
                  {cardStyle}
                  </div>
              ))}
            </div>
     </div>
     <div ref={miriRef}>
     <MiriMessages message={message} buttons={null} needMsgHideEffect={false}/>
     </div>
    </>
    )
}

export default ChooseCards;