import React, { useState, useEffect, useRef } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAnglesDown } from '@fortawesome/free-solid-svg-icons'

import { MIRI_CHAT_BOX_LIMIT_HEIGHT, MAX_SCREEN_WIDTH } from '../Constant'

const SETTINGS = {
  introduction: {
    height: 'h-[30rem]',
    hideHeight: '-bottom-72',
    moveHeight: '-translate-y-72',
    chatStyleClass: 'h-[8rem]',
    expandChatClass: 'h-[18rem] overflow-y-auto'
  },
  default: {
      height: 'h-[32rem]',
      hideHeight: '-bottom-64',
      moveHeight: '-translate-y-64',
      chatStyleClass: 'h-[8rem]',
      expandChatClass: 'h-[20rem] overflow-y-auto'
  },
  explanation: {
    height: 'h-[36rem]',
    hideHeight: '-bottom-96',
    moveHeight: '-translate-y-80',
    chatStyleClass: 'h-[8rem]',
    expandChatClass: 'h-[22rem] overflow-y-auto'
  },
  poe: {
    height: 'h-[18rem]',
    hideHeight: 'bottom-0',
    moveHeight: '',
    chatStyleClass: 'h-[8rem] overflow-y-auto',
    expandChatClass: 'h-[22rem] overflow-y-auto'
  }
}


function MiriAvatar(){
  return (
    <>
        <div className="flex border h-16 w-16 divination-miri-style rounded-full">
          <img src="./favicon.jpg" className="rounded-full"></img>
          <p className="pl-1 pt-12 font-bold">Miri</p>
        </div>
    </>
  )
}


function MiriMessages({message, buttons, hideEffect, isExpand, settings}){

  const selectedSettings = SETTINGS[settings] || SETTINGS.default;

  return (
    <>
            <div className={`${MAX_SCREEN_WIDTH} mx-auto ${selectedSettings.height} p-5 fixed inset-x-0 ${selectedSettings.hideHeight} z-[100] rounded-t-[20px] border divination-message-section ${hideEffect && isExpand.current ? selectedSettings.moveHeight : ''}`}>
              <MiriAvatar/>
            <div className={`mt-4 ${hideEffect && isExpand.current ? selectedSettings.expandChatClass : selectedSettings.chatStyleClass}`}>
                {message}
            </div>
            { hideEffect && isExpand.current && <div className='mt-4'><div className='flex justify-center'>{buttons}</div></div>}
            {buttons && !hideEffect && <div className='fixed inset-x-0 bottom-5'><div className='flex justify-center'>{buttons}</div></div>}
        </div>
    
    </>
  )

}

export { MiriMessages };