// TODO Need to put position in front of title (in cover-mode and single result)
function TarotPrediction({method, arrangeType, chosenCardCounts}){

    // Switch Stage based on stage
    switch(arrangeType) {
        case 1:
            return <ModuleOne method={method} chosenCardCounts={chosenCardCounts} />
        
        case 2:
            return <ModuleThree method={method} chosenCardCounts={chosenCardCounts} />

        case 3:
            return <ModuleCross method={method} chosenCardCounts={chosenCardCounts} />

    }
}

export default TarotPrediction;
