import React, { useState, useEffect } from 'react';

// import { MoonBlockUp, MoonBlockCover } from "./Component";

import PoeCover from '../../static/poe-cover.png'
import PoeUp from '../../static/poe-up.png'

function MoonBlockUp({}){
    return (
        <>
            <img className="h-32" src={PoeUp} alt="poe up side" />
        </>
    )
}

function MoonBlockCover(){
    return (
        <>
        <img className="h-32" src={PoeCover} alt="poe cover side" />
        </>
    )
}

function HappyCupOne({leftRotation, rightRotation}){

    return (
        <>
            <div key="moon-block-happycup1-up" style={{transform: `rotate(${leftRotation}deg)`}}><MoonBlockUp /></div>
            <div key="moon-block-happycup1-cover" style={{transform: `rotate(${rightRotation}deg)`}}><MoonBlockCover /></div>
        </>
    )

}

function HappyCupFour({leftRotation, rightRotation}){

    return (
        <>
            <div key="moon-block-happycup4-cover" style={{transform: `rotate(${leftRotation}deg)`}}><MoonBlockCover /></div>
            <div key="moon-block-happycup4-up" style={{transform: `rotate(${rightRotation}deg)`}}><MoonBlockUp /></div>
        </>
    )

}


function LaughCup({leftRotation, rightRotation}){

    return (
        <>
            <div key="moon-block-laughcup-1" style={{transform: `rotate(${leftRotation}deg)`}}><MoonBlockUp /></div>
            <div key="moon-block-laughcup-2" style={{transform: `rotate(${rightRotation}deg)`}}><MoonBlockUp /></div>
        </>
    )

}


function AngryCup({leftRotation, rightRotation}){

    return (
        <>
            <div key="moon-block-angrycup-1" style={{transform: `rotate(${leftRotation}deg)`}}><MoonBlockCover /></div>
            <div key="moon-block-angrycup-2" style={{transform: `rotate(${rightRotation}deg)`}}><MoonBlockCover /></div>
        </>
    )
    
}

function HandlePrediction({ code }){
    const [rotationLeftCup, setRotationLeftCup] = useState(0);
    const [rotationRightCup, setRotationRightCup] = useState(0);

    useEffect(() => {
        const randomRotationLeftCup = Math.round(Math.random() * 360); 
        setRotationLeftCup(randomRotationLeftCup)
        const randomRotationRightCup = Math.round(Math.random() * 360);
        setRotationRightCup(randomRotationRightCup)
      }, []);


    switch(code) {
        // Happy Cup, up-cover
        case '1':
            return <HappyCupOne leftRotation={rotationLeftCup} rightRotation={rotationRightCup} />
        
        // Laugh Cup 
        case '2':
            return <LaughCup leftRotation={rotationLeftCup} rightRotation={rotationRightCup} />

        // Angry Cup
        case '3':
            return <AngryCup leftRotation={rotationLeftCup} rightRotation={rotationRightCup} />
        
        // Happy Cup, cover-up
        case '4':
            return <HappyCupFour leftRotation={rotationLeftCup} rightRotation={rotationRightCup} />

    }
}

export default HandlePrediction;