import { useNavigate } from "react-router-dom";

import { useTranslation } from 'react-i18next';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHouseChimney, faGear, faCircleInfo} from '@fortawesome/free-solid-svg-icons'

import { MAX_SCREEN_WIDTH } from './Constant'

import CrystalBall from './Element';

function Navbar({setRouteFunc}){
    const navigate = useNavigate();
    const { t } = useTranslation();

    const handleChangeRoute = (value) =>{
      const userPathMonitor = {
        route: value,
      };
      // Save localStorage
      localStorage.setItem('userPathMonitor', JSON.stringify(userPathMonitor));
      setRouteFunc(value)
    }
    return (
      <>
      {/* Fixed at bottom: fixed inset-x-0 bottom-0 */}
      <div className={`${MAX_SCREEN_WIDTH} mx-auto h-24 border-t navbar-color fixed inset-x-0 bottom-0`}>
      <div className="">
        <div className="grid grid-cols-5 pt-4">
            <div className='p-4 text-center font-bold main-font-color'>
                <button onClick={() => handleChangeRoute("")}>
                <div className="pb-1"><FontAwesomeIcon icon={faHouseChimney} size="xl" style={{color: "#f8d781",}} /></div>
                <div>
                {t('home.navbar.home')}
                </div>
                </button>
            </div>
          <div className='p-4 text-center font-bold main-font-color'></div>
          {/* Magic Ball */}
          <div className='p-4 text-center font-bold main-font-color'><CrystalBall/></div>
          <div className='p-4 text-center font-bold main-font-color'>
          <button onClick={() => handleChangeRoute("settings")}>
            <div className="pb-1"><FontAwesomeIcon icon={faGear} size="xl" style={{color: "#f8d781",}} /></div>
            <div>
              {t('home.navbar.settings')}
              </div>
              </button>
            </div>
            <div className='p-4 text-center font-bold main-font-color'>
            <button onClick={() => handleChangeRoute("about")}>
              <div className="pb-1"><FontAwesomeIcon icon={faCircleInfo} size="xl" style={{color: "#f8d781",}} /></div>
              <div>
              {t('home.navbar.about')}
              </div>
              </button>
            </div>
        </div>
  
        </div>
      </div>
      </>
    )
  }

export default Navbar;