// import logo from './logo.svg';

import React, { useState, useEffect } from 'react';

import { Helmet } from 'react-helmet-async';

import { useTranslation } from 'react-i18next';

import './App.css';
import DivinationRoom from './Divination'
import Settings from './Settings'
import About from './About';
import Navbar from './Common'

import { getUserLanguage } from './divinations/Component'

import RuneIcon from './static/rune-icon.png'
import TarotIcon from './static/tarot-icon.png'
import LenormandIcon from './static/lenormand-icon.png'
import MoonBlockIcon from './static/moon-block-icon.png'
import WhisperIcon from './static/whisper-icon.png'
import WisdomIcon from './static/wisdom-icon.png'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFeatherPointed, faHatWizard } from '@fortawesome/free-solid-svg-icons'


function HomePageInfo({setRouteFunc, setMethodFunc}){
  const { t } = useTranslation();


  const handleClick = (method) => {
    // Set Divination method
    setMethodFunc(method);

    // Navigate to divination
    setRouteFunc("divination");
    
    const userPathMonitor = {
      route: "divination",
      method: method,
      state: "",
      // ...
    };
    // Save localStorage
    localStorage.setItem('userPathMonitor', JSON.stringify(userPathMonitor));
  };

  return (
    <>
    <div className='relative bg-gradient-to-br from-white/40 to-black to-70% grid place-items-center mb-12 bg-black rounded-[20px] border home-page-info-border'>
      <p className='text-center text-xl font-bold home-info-font-color pt-14 pb-7'>{t('home.question.title')}</p>
      <div className='rounded-xl border golden-border-color deeper-blue-color py-1 px-7 mb-6'>
        <button onClick={() => handleClick("runes")}>
        <p className='text-center text-sm font-bold home-info-font-color'>{t('home.question.button')}</p>
        </button>
      </div>
      <p className='text-center text-sm font-bold home-page-info-font-color pb-1'>{t('home.question.miriName')}</p>
      <div className="absolute -right-3 bottom-0 rotate-6"><FontAwesomeIcon icon={faHatWizard} size="4x" style={{color: "#c5a4e4",}} /></div>
    </div>
    </>
  )

}


function DivinationMenu({setRouteFunc, setMethodFunc}){
  const { t } = useTranslation();

  const handleClick = (method) => {
    // Set Divination method
    setMethodFunc(method);

    // Navigate to divination
    setRouteFunc("divination");
    
    const userPathMonitor = {
      route: "divination",
      method: method,
      state: "",
      // ...
    };
    // Save localStorage
    localStorage.setItem('userPathMonitor', JSON.stringify(userPathMonitor));
  };

  const divinationMethods = [
    [t('home.menu.rune'), 'runes', RuneIcon],
    [t('home.menu.tarot'), 'tarot', TarotIcon],
    [t('home.menu.lenormand'), 'lenormand', LenormandIcon],
    [t('home.menu.poe'), 'poe', MoonBlockIcon],
    [t('home.menu.nature'), 'whispers', WhisperIcon],
    [t('home.menu.whisper'), 'wisdom', WisdomIcon],
    // inactive these 2 methods, TODO change to gray
    // ['', 'ichin', 'disable'],
    // ['', 'kauchin', 'disable']
  ]

  return (
    <>
    <div className='relative home-divination-menu-wallpaper rounded-[20px] px-2 pt-1 pb-4 border trans-white-border'>
    <div className="absolute -left-4 -top-4"><FontAwesomeIcon icon={faFeatherPointed} size="3x" style={{color: "#ffffff",}} /></div>
      <div>
      <div className="grid grid-cols-4 text-center font-bold text-white">
        
        {divinationMethods.map(method => (
            <div key={method[1]} className='mt-3'>
              <button onClick={() => handleClick(method[1])} className="mt-2">
              {/* Icon, CSS need adjust */}
              <div className='relative h-16 w-16 border m-auto rounded-full mb-1'>
                <div className='absolute inset-0 border bg-white rounded-full m-2 p-2'>
                <img src={method[2]} alt={`${method[1]} Icon`} />
                </div>
              </div>
                {method[0]}
              </button>
              </div>
        ))}

      </div>
      </div>
      {/* side-points */}
      <div></div>
    </div>
    </>
  )
}

function HomePage({setRouteFunc, setMethodFunc, languageTrans}){
  const { t } = useTranslation();

  return (
    <>
    <div className={`wallpaper wallpaper-color ${languageTrans === 'en' ? 'font-acme' : ''}`}>
    <div className='px-6 pt-8'>
    <HomePageInfo setRouteFunc={setRouteFunc} setMethodFunc={setMethodFunc}/>
    <DivinationMenu setRouteFunc={setRouteFunc} setMethodFunc={setMethodFunc}/>
    <div className='pt-6'>
      <p className='text-center text-sm home-info-font-color'>{t('home.ver')}</p>
    </div>
    </div>
    <Navbar setRouteFunc= {setRouteFunc}/>
    </div>
    </>
  )
}


// Wallpaper render to this layer.
function App() {

  const { t } = useTranslation();

  // Switch route
  const [route, setRoute] = useState('');
  const setRouteValue = (value) => {
    setRoute(value);
  };

  // This is set for i18n translation and html lang setting.
  const [languageTrans, setlanguageTrans] = useState('')

  useEffect(() => {
    const userLanguage = getUserLanguage();
    setlanguageTrans(userLanguage)

    // Get route from localStorage
    const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
    if (localData){
      if (localData.route){
        setRouteValue(localData.route)
      }
    }
  }, []);

  // Set Divination method
  const [method, setMethod] = useState('');
  const setMethodValue = (value) => {
    setMethod(value);
  };

  // Switch Component based on route
  switch(route) {
    case "":
      return (
        <>
            <Helmet htmlAttributes={{ lang: languageTrans }}>
              <title>{t('head.title')}</title>
              <meta name="description" content={t('head.description')} />
              <meta property="og:title" content={t('head.title')} />
              <meta property="og:description" content={t('head.description')} />
            </Helmet>
          <HomePage setRouteFunc={setRouteValue} setMethodFunc={setMethodValue} languageTrans={languageTrans}/>
        </>
        )
      case "divination":
        return (
          <>
            <Helmet htmlAttributes={{ lang: languageTrans }}>
              <title>{t('head.title')}</title>
              <meta name="description" content={t('head.description')} />
              <meta property="og:title" content={t('head.title')} />
              <meta property="og:description" content={t('head.description')} />
            </Helmet>
          <DivinationRoom methodValue={method} setRouteFunc={setRouteValue} />
          </>
          )
      case "settings":

        return (
          <>
          <Helmet htmlAttributes={{ lang: languageTrans }}>
          <title>{t('head.title')}</title>
          <meta name="description" content={t('head.description')} />
          <meta property="og:title" content={t('head.title')} />
          <meta property="og:description" content={t('head.description')} />
        </Helmet>
        <Settings setRouteFunc={setRouteValue} />
        </>
        )
      
      case "about":
        return (
          <>
            <Helmet htmlAttributes={{ lang: languageTrans }}>
              <title>{t('head.title')}</title>
              <meta name="description" content={t('head.description')} />
              <meta property="og:title" content={t('head.title')} />
              <meta property="og:description" content={t('head.description')} />
            </Helmet>
          <About setRouteFunc={setRouteValue} />
          </>
        )

    }

}

export default App;
