import React, { useState, useEffect, useRef } from 'react';

import { useTranslation } from 'react-i18next';

import { getUserLanguage, fetchData, PolyButton, LoadingPage } from '../Component';
import { MiriMessages } from '../Miri';

import HandleModule from './arrangement/Module';
import HandleDescription from './arrangement/Description';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';


function ArrangeComponent ({method, counts}){

  return (
      <>          
           <HandleModule method={method} counts={counts}/>
      </>
  )
}


function Arrangement({method, setStageFunc, setCardCounts, setArrangeTypeFunc, selectedStyle}){
    // ++
    const { t } = useTranslation();

    const overlay = selectedStyle.arrangeOverlayStyle;

    const [arrangeData, setarrangeData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    const [selectedDiv, setSelectedDiv] = useState(null);
    const divRef = useRef(null);
    const miriRef = useRef(null);
    
    let body = {
        "method": method,
        "action": "arrangement",
    }

    const [currentSlide, setCurrentSlide] = useState(0);

        const settings = {
        // dots: true,
        infinite: false,
        arrows: true,
        speed: 500,
        slidesToShow: 2,
        slidesToScroll: 1,
        // customPaging: function (i) {
        //     // i  dots  dots 
        //     return (
        //       <div
        //         style={{
        //           width: '10px',
        //           height: '10px',
        //           backgroundColor: currentSlide === i ? '#F8D781' : 'transparent', // 
        //           border: `1px solid #F8D781`, // 
        //           borderRadius: '50%',
        //           cursor: 'pointer',
        //         }}
        //       ></div>
        //     );
        //   },
          beforeChange: (current, next) => setCurrentSlide(next),
      };

    useEffect(() => {
        //  API
        const detectData = async () => {
        try {
          const userLanguage = getUserLanguage();
          const localData = JSON.parse(localStorage.getItem('systemData')) || {};
          
          if (localData.arrangeData) {

            if (localData.arrangeData[userLanguage][method]) {
              setarrangeData(localData.arrangeData[userLanguage][method]);
              setIsLoading(false);
            } else {
              const data = await fetchData(body);
              localData.arrangeData[userLanguage] = {
                ...localData.arrangeData[userLanguage],
                [method]: data
              }
              localStorage.setItem('systemData', JSON.stringify(localData));

              setarrangeData(data);
              setIsLoading(false);
            }
          } else {
            const data = await fetchData(body);
            const zhTW = "zh-TW"
            const saveLocalData = {
              arrangeData: {
                en: {

                },
                [zhTW]:{

                }
              }
            }

            saveLocalData.arrangeData[userLanguage] = {
              [method]: data
            } 
            localStorage.setItem('systemData', JSON.stringify(saveLocalData));
            setarrangeData(data);
            setIsLoading(false);
          }
        } catch (error) {
          console.error(error)
        }
      };
      detectData();
      }, []);

    const initMessage = t('divination.arrangement.initMessage')
    const changeStage = () => {
        setStageFunc('choose_cards');
      };

    const [message, setMessage] = useState(initMessage);
    const [hideEffect, setHideEffect] = useState(false);
    const [buttons, setButtons] = useState(null);
    const expandRef = useRef(false);

    const handleDivClick = (divId, counts) => {
        if (selectedDiv == null) {
            setSelectedDiv(divId);
            const desiredData = arrangeData.find(item => item.id === divId);
            const checkMessage = (
                <React.Fragment>
                  {/* <PolyButton func={changeStage} title={''}/> */}
                <HandleDescription method={method} counts={counts} message={desiredData.description} />
                  {/* <p>{desiredData.description}</p> */}
                  
                </React.Fragment>
              );
            const CheckButtons = (
              <React.Fragment>
                <PolyButton func={changeStage} title={t('divination.buttons.choose')}/>
              </React.Fragment>
            )
            setButtons(CheckButtons);
            setMessage(checkMessage);
            setHideEffect(true);
            expandRef.current = true;
            setArrangeTypeFunc(divId)
            setCardCounts(counts)
        } else if (divId===selectedDiv) {
            setSelectedDiv(null);
            setButtons(null)
            setMessage(initMessage);
            setHideEffect(false);
            expandRef.current = false;
            setArrangeTypeFunc('')
            setCardCounts(0)
        } else {
            setSelectedDiv(divId);
            const desiredData = arrangeData.find(item => item.id === divId);
            const checkMessage = (
                <React.Fragment>
                  {/* <PolyButton func={changeStage} title={''}/> */}
                    <HandleDescription method={method} counts={counts} message={desiredData.description} />
                {/* <p>{desiredData.description}</p> */}
                </React.Fragment>
              );
            const CheckButtons = (
              <React.Fragment>
                <PolyButton func={changeStage} title={''}/>
              </React.Fragment>
            )
            setButtons(CheckButtons)
            setMessage(checkMessage);
            setHideEffect(true);
            expandRef.current = true;
            setArrangeTypeFunc(divId)
            setCardCounts(counts)
        }
        
      };

    const handleClickOutside = (event) => {
        if (divRef.current && !divRef.current.contains(event.target)) {
          if (miriRef.current && miriRef.current.contains(event.target)) {
            // do nothing
          } else {
            setSelectedDiv(null);
            setButtons(null)
            setMessage(initMessage);
            setHideEffect(false);
            expandRef.current = false;
            setCardCounts(0)
            }
          }
        };

    useEffect(() => {
        document.addEventListener('click', handleClickOutside);
        return () => {
          document.removeEventListener('click', handleClickOutside);
        };
      }, []);
    
      return (
        <>
        <div className='pt-24 px-8'>
        {isLoading ? (
            <LoadingPage />

        ) : (
            <div ref={divRef} className='text-center'>
                          <Slider {...settings}>
            {arrangeData.map((arrange) => (
              <React.Fragment key = {`div${arrange.id}`}>
                <div onClick={() => handleDivClick(arrange.id, arrange.counts)} className='relative floating-div rounded-[20px] grid justify-items-center hover:cursor-pointer'>
                        {selectedDiv === arrange.id && <div className="overlay-arrange" style={overlay}></div>}
                        <ArrangeComponent method={method} counts={arrange.counts} />
                        <p className={`mt-2 font-bold divination-arrange-word ${t('lang') === 'en' ? 'font-acme' : ''}`} style={selectedStyle.arrangeWordColor}>{arrange.title}</p>
                </div>
                </React.Fragment>
            ))}
            </Slider>
        </div>

        )}
         </div>
         <div ref={miriRef}>
          <MiriMessages message={message} buttons={buttons} hideEffect={hideEffect} isExpand={expandRef}/>
          </div>
        </>
    )
}

export default Arrangement;