import React, { useState, useEffect, useRef } from 'react';

import { useTranslation } from 'react-i18next';

import { PolyButton } from '../Component';
import { MiriMessages } from '../Miri';

import { ExpandFixedCardsGenerator } from './Animation'


function Meditation({ setStageFunc, selectedStyle }){
    const { t } = useTranslation();

    const overlay = selectedStyle.overlayStyle;
    const cardStyle = selectedStyle.cardStyle;
    
    const initMessage = t('divination.meditation.initMessage')
    const changeStage = () => {
      // AB
        handleCloseAnimate();
        setTimeout(() => {
          setStageFunc('arrangement');
        }, 4000);
      };
    const checkMessage = (
        <React.Fragment>
          <p>{t('divination.meditation.checkMessage')}</p>
          <div className='flex justify-center pt-4'><PolyButton func={changeStage} title={t('divination.buttons.sure')}/></div>
        </React.Fragment>
      );
    const [selectedDiv, setSelectedDiv] = useState(null);
    const [message, setMessage] = useState(initMessage);
    const divRef = useRef(null);
    const miriRef = useRef(null);

    const selectedCardRef = useRef(0);

    // console.log(selectedDiv)

    const handleDivClick = (divId) => {
        if (selectedDiv == null) {
          selectedCardRef.current = divId
            setSelectedDiv(divId);
            setMessage(checkMessage);
        } else {

            if (selectedDiv === divId) {
                setSelectedDiv(null);
                setMessage(initMessage);
            } else {
              selectedCardRef.current = divId
                setSelectedDiv(divId);
                setMessage(checkMessage);
            }

        }
        
      };

    const handleClickOutside = (event) => {
    if (divRef.current && !divRef.current.contains(event.target)) {

        if (miriRef.current && miriRef.current.contains(event.target)) {

        } else {
          selectedCardRef.current = null
          setSelectedDiv(null);
          setMessage(initMessage);
          }
    }
    };
    // 

    const [isSpread, setIsSpread] = useState(false);
    const [activeCards, setActiveCards] = useState([]);
    const [resetZIndex, setResetZIndex] = useState([]);

    const handleExpandAnimate = () => {
      setIsSpread(!isSpread);
    }

    const handleCloseAnimate = () => {
      const endValue = 21;
      //  Array.from 
      const selectedCardNo = selectedCardRef.current
      const newCards = Array.from({ length: endValue - selectedCardNo + 1 }, (_, index) => selectedCardNo + index);
      setActiveCards(prevState => [...prevState, ...newCards]);
      setResetZIndex(prevState => [...prevState, ...newCards]);
      setTimeout(() => {
        setIsSpread(false);
        setTimeout(() => {
          setActiveCards([]);
        }, 1000);
      }, 1000);
    }

    // Gap: x-2(8px), 12px, 16px
    const cardGap = [
      // 16px
    [21, 'translate-x-[216px]', 'translate-x-[216px]', 'z-[70]', 'z-[21]'], [20, 'translate-x-[200px]', 'translate-x-[212px]', 'z-[69]', 'z-[20]'], 
    [19, 'translate-x-[184px]', 'translate-x-[208px]', 'z-[68]', 'z-[19]'],
      // 12px
    [18, 'translate-x-[168px]', 'translate-x-[204px]', 'z-[67]', 'z-[18]'], [17, 'translate-x-[156px]', 'translate-x-[200px]', 'z-[66]', 'z-[17]'], [16, 'translate-x-[144px]', 'translate-x-[196px]', 'z-[65]', 'z-[16]'],
    [15, 'translate-x-[132px]', 'translate-x-[192px]', 'z-[64]', 'z-[15]'], [14, 'translate-x-[120px]', 'translate-x-[188px]', 'z-[63]', 'z-[14]'], [13, 'translate-x-[108px]', 'translate-x-[184px]', 'z-[62]', 'z-[13]'],
    // 8px
    [12, 'translate-x-[96px]', 'translate-x-[184px]', 'z-[61]', 'z-[12]'],[11, 'translate-x-[88px]', 'translate-x-[184px]', 'z-[60]', 'z-[11]'], 
    [10, 'translate-x-[80px]', 'translate-x-[184px]', 'z-[59]', 'z-[10]'], [9, 'translate-x-[72px]', 'translate-x-[184px]', 'z-[58]', 'z-[9]'], [8, 'translate-x-[64px]', 'translate-x-[184px]', 'z-[57]', 'z-[8]'], 
    [7, 'translate-x-14', 'translate-x-[184px]', 'z-[56]', 'z-[7]'], [6, 'translate-x-12', 'translate-x-[184px]', 'z-[55]', 'z-[6]'], [5, 'translate-x-10', 'translate-x-[184px]', 'z-[54]', 'z-[5]'], 
    [4, 'translate-x-8', 'translate-x-[184px]', 'z-[53]', 'z-[4]'], [3, 'translate-x-6', 'translate-x-[184px]', 'z-[52]', 'z-[3]'], [2, 'translate-x-4', 'translate-x-[184px]', 'z-[51]', 'z-[2]'], 
    [1, 'translate-x-2', 'translate-x-[184px]', 'z-[50]', 'z-[1]'], [0, '', 'translate-x-[184px]', 'z-[49]', 'z-[0]']
  ]

    const fixedCard = ExpandFixedCardsGenerator();

    useEffect(() => {
      handleExpandAnimate();
        document.addEventListener('click', handleClickOutside);
        return () => {
          document.removeEventListener('click', handleClickOutside);
        };
      }, []);

    return (
        <>
            <div className='pt-24 px-8'>
            <div ref={divRef} className='relative'>
            {cardGap.map((gap, index) => (
                <div key = {`"div${index}"`} onClick={() => handleDivClick(gap[0])} className={`transition-all delay-300 ease-linear duration-500 drop-shadow-md absolute left-4 ${isSpread && gap[1]} top-0 ${resetZIndex.includes(gap[0]) ? gap[4] : gap[3]} ${activeCards.includes(gap[0]) && gap[2]}`}>
                    {selectedDiv === gap[0] && <div className="overlay" style={overlay}></div>}{cardStyle}</div>
                ))}
            {fixedCard.map((gap, index) => (
                <div key = {`"div-fixed-${index}"`} className='drop-shadow-md' style={gap}>
                      {cardStyle}
                    </div>
                ))}
            </div>
            {/*  */}
        </div>
        <div ref={miriRef}>
          {/* <button onClick={handleCloseAnimate} className='relative'>123</button> */}
          <MiriMessages message={message} buttons={null} needMsgHideEffect={false}/>
        </div>
        </>
    )

}

export default Meditation;