import React, { useState, useEffect } from 'react';

import { useTranslation } from 'react-i18next';

import { MiriMessages } from '../Miri';
import { PolyButton, fetchData, LoadingPage } from '../Component';
// import { MoonBlockUp, MoonBlockCover } from "./Component";
import HandlePrediction from './Module';

import PoeCover from '../../static/poe-cover.png'
import PoeUp from '../../static/poe-up.png'

// Moon Block doesn't save result in localStorage


function HandleDescription({message}){

    return (
        <>
            {message.data.meaning ? (
                <>
                {message.data.meaning.map((ms, index) => (
                    <p key={`meaning-${index}`}>{ms}</p>
                ))}
                </>
            ) : (
                <p>{message.data.description}</p> 
            )}
        </>
    )

}

const updatePredictResult = ({setResult, setIsLoading, method, chosenCardCounts}) => {
 
    let body = {
        "method": method,
        "action": "prediction",
        "counts": chosenCardCounts
    }
    setIsLoading(true);
    const detectData = async () => {
    try{
        const data = await fetchData(body);
        setResult(data);
        setIsLoading(false);
      } catch (error) {
        console.error(error)
      }
    };
    detectData();

};


function Prediction({method, setStageFunc, chosenCardCounts}){
    const { t } = useTranslation();

    const [result, setResult] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
    //  API
        updatePredictResult({setResult, setIsLoading, method, chosenCardCounts})
    }, []);

    const message = (
        <>
            {isLoading ? (
               <LoadingPage/>
            ) : (

                <React.Fragment>
                    <div className='flex gap-1'>
                    <p>{t('divination.prediction.MessageMoonBlock')}</p> 
                    <p className='font-bold'>{result.data.title}</p> 
                    </div>
                    {/* <p>{result.data.description}</p>  */}
                    <HandleDescription message={result}/>
                    {/* <div className='flex justify-center'>
                        <PolyButton func={() => updatePredictResult({setResult, setIsLoading, method, chosenCardCounts})} title={''}/>
                    </div> */}
                </React.Fragment>
                
            )}
        </>
        )
    
    const button = (
        <>
        {isLoading ? (
           <></>
        ) : (
            // TODO handle over border
            <React.Fragment>
                <div className='flex justify-center'>
                    <PolyButton func={() => updatePredictResult({setResult, setIsLoading, method, chosenCardCounts})} title={t('divination.buttons.again')}/>
                </div>
            </React.Fragment>
            
        )}
    </>
    )

    return (
        <>
        {isLoading ? (
            <>
                <LoadingPage/>
                <MiriMessages message={""} buttons={null} settings={'poe'}/>
            </>
        ) : (
            <div>
            <div className="pt-28 px-8">
                <div className='border-2 moon-block-cloth p-4'>
                    <div className='h-48 md:h-56 grid grid-cols-2 border-2 moon-block-inner-cloth p-2 gap-8'>
                        <HandlePrediction code={result.data.code} />
                    </div>
                </div>
            </div>
                <MiriMessages message={message} buttons={button} settings={'poe'}/>
            </div>
        ) 
        }
        </>
    )
}


function Meditation({setStageFunc}){
    const { t } = useTranslation();
    // TODO call API?? or local i18n
    const changeStage = (stage) => {
        // Animate?
    
        // Navigate to divination
        setStageFunc(stage);
      };

    const message = (
        <>
            <p>{t('divination.meditation.initMessageMoonBlock')}</p>
            <p>{t('divination.meditation.noteMessageMoonBlock')}</p>
        </>
        )

        const button = (
            <React.Fragment>
            <div className='flex justify-center'>
            <PolyButton func={() => changeStage("explanation")} title={t('divination.buttons.throw')}/>
            </div>
        </React.Fragment>
        )

    return (
        <>
        <div className='pt-48 px-8'>
        <div className='border-2 moon-block-cloth p-4'>
            <div className='h-48 md:h-56 flex justify-center border-2 moon-block-inner-cloth p-2 gap-8'>
            <div className='grid place-items-center'>
                        {/* <MoonBlockCover/> */}
                        <img className="h-32 rotate-[24deg]" src={PoeCover} alt="poe cover side" />
                    </div>
                    <div className='grid place-items-center'>
                        {/* <MoonBlockUp/> */}

                        <img className="h-32 rotate-[24deg]" src={PoeUp} alt="poe up side" />
                    </div>
            </div>
            </div>
        </div>
        <MiriMessages message={message} buttons={button} settings={'poe'}/>
        </>
    )
}

function Guide({method, changeStage}){
    const { t } = useTranslation();

    const [data, setData] = useState("")
    const [isLoading, setIsLoading] = useState(true);

    let body = {
        "method": method,
        "action": "guide",
    }

    useEffect(() => {
        //  API
        const detectData = async () => {
        try{
            const data = await fetchData(body);
            setData(data);
            setIsLoading(false);
          } catch (error) {
            console.error(error)
          }
        };
        detectData();
      }, []);

    const message = (
        <React.Fragment>
            <p>{data}</p> 
            {/* <div className='flex justify-center'>
                <PolyButton func={() => changeStage("meditation")} title={''}/>
            </div> */}
        </React.Fragment>
        )
    
        const button = (
            <React.Fragment>
            <div className='flex justify-center'>
                <PolyButton func={() => changeStage("meditation")} title={t('divination.buttons.enter')}/>
            </div>
        </React.Fragment>
        )
    
        const loadingMessage = (
            <div className="flex justify-center loading-text mt-10">
            <span className="dot1"></span>
            <span className="dot2"></span>
            <span className="dot3"></span>
        </div>
        )

    return (
        <>
        {isLoading ? (
            <MiriMessages message={loadingMessage} buttons={false} settings={'poe'}/>

        ) : (
            <MiriMessages message={message} buttons={button} settings={'poe'}/>
        )
        }
        
        </>
    )

}


function Introduction({method, setStageFunc}){
    const { t } = useTranslation();
    const [isGuide, setGuide] = useState(false)

    const changeToGuide = () => {
        setGuide(true);
      };

    const message = (
        <>
            <p>{t('divination.introduction.hello')}</p>
            <p>{t('divination.introduction.welcome')}{t(`divination.method.${method}`)}{t('divination.introduction.guideMoonBlock')}</p>
        </>
        )
    const buttons = (
        <>
            <div className='flex justify-center gap-2 pt-4'>
                <PolyButton func={() => changeToGuide()} title={t('divination.buttons.introduce')}/>
                <PolyButton func={() => changeStage("meditation")} title={t('divination.buttons.choose')}/>
            </div>
        </>
    )

    const changeStage = (stage) => {
        setStageFunc(stage);
      };

    return (
        <>
        <div className='pt-48 px-8'>
            <div className='border-2 moon-block-cloth p-4'>
            <div className='h-48 md:h-56 flex justify-center border-2 moon-block-inner-cloth p-2 gap-8'>
                {/* <div className='moon-block-cloth p-8'> */}
                    <div className='grid place-items-center'>
                        {/* <MoonBlockCover/> */}
                        <img className="h-32 rotate-[24deg]" src={PoeCover} alt="poe cover side" />
                    </div>
                    <div className='grid place-items-center'>
                        {/* <MoonBlockUp/> */}

                        <img className="h-32 rotate-[24deg]" src={PoeUp} alt="poe up side" />
                    </div>
                {/* </div> */}
                </div>
            </div>
        </div>

        {isGuide ? (
            <Guide method={method} changeStage={changeStage} />

        ) : (
            <MiriMessages message={message} buttons={buttons} settings={'poe'}/>
        )}
        </>
    )
}


function MoonBlock({method}){

    const [stage, setStage] = useState('');

    const setStageFunc = (value) => {
        setStage(value);
    };

    // Switch Stage based on stage
    switch(stage) {
        case "":
            // 
            return <Introduction method={method} setStageFunc={setStageFunc}/>
        
        case "meditation":
            // 
            // 
            return <Meditation setStageFunc={setStageFunc}/>
        
        case "explanation":
            // 
            // 
            // 
            return <Prediction method={method} setStageFunc={setStageFunc} chosenCardCounts={1}/>

    }

}

export default MoonBlock;