import { CARD_STYLE_SETTINGS } from '../../../Constant'


function OneCardStyle({cardStyle, cardSvg}){

    return (
        <>
        <div key="arrange-module1-1" className={`h-16 w-12 rounded-[5px] border-2 ${cardStyle} grid place-items-center pt-1 m-1 drop-shadow-lg`}>
            {cardStyle==='divination-rune-card' ? <>
            <div><img src={cardSvg} alt="card SVG" className='h-8 w-8' /></div>
            </> : <>
            <div><img src={cardSvg} alt="card SVG" className='h-14 w-12' /></div>
            </>}
            
        </div>
        </>
    )

}

function TwoCardStyle({uniqueKey, cardStyle, cardSvg}){

    return (
        <>
        <div key={uniqueKey} className='h-14 w-10 rounded-[5px] border-2 divination-card pt-1 m-1 drop-shadow-lg'>
        {cardStyle==='divination-rune-card' ? <>
            <div><img src={cardSvg} alt="card SVG" className='h-6 w-6' /></div>
            </> : <>
            <div><img src={cardSvg} alt="card SVG" className='h-12 w-10' /></div>
            </>}
        </div>
        </>
    )

}

function ThreeCardStyle({uniqueKey, cardStyle, cardSvg}){

    return (
        <>
        <div key={uniqueKey} className={`h-12 w-8 rounded-[5px] grid place-items-center border-2 ${cardStyle} pt-1 m-1 drop-shadow-lg`}>
        {cardStyle==='divination-rune-card' ? <>
            <div><img src={cardSvg} alt="card SVG" className='h-6 w-6' /></div>
            </> : <>
            <div><img src={cardSvg} alt="card SVG" className='h-10 w-8' /></div>
            </>}
        </div>
        </>
    )

}

function CrossCardStyle({uniqueKey, cardStyle, cardSvg}){

    return (
        <>
        <div key={uniqueKey} className={`h-10 w-8 rounded-[5px] grid place-items-center border-2 ${cardStyle} drop-shadow-lg`}>
            {cardStyle==='divination-rune-card' ? <>
                    <div><img src={cardSvg} alt="card SVG" className='h-6 w-6' /></div>
                </> : <>
                    <div><img src={cardSvg} alt="card SVG" className='h-8 w-8' /></div>
                </>
            }
        </div>
        </>
    )

}


function ModuleOne({cardStyle, cardSvg, borderColor}){


    return (
        <>
            <div key="arrange-module-1" className='grid h-36 w-36 rounded-[20px] border divination-arrange-component place-items-center' style={borderColor}>
                    <OneCardStyle cardStyle={cardStyle} cardSvg={cardSvg}/>
            </div>

        </>
    )

}

function ModuleTwo({cardStyle, cardSvg, borderColor}){
    
    return (
        <>
            <div key="arrange-module-2"className='flex justify-center h-36 w-36 rounded-[20px] border divination-arrange-component place-items-center' style={borderColor}>

                <TwoCardStyle uniqueKey="arrange-module2-1" cardStyle={cardStyle} cardSvg={cardSvg}/>
                <TwoCardStyle uniqueKey="arrange-module2-2" cardStyle={cardStyle} cardSvg={cardSvg}/>

            </div>

        </>
    )
}

function ModuleThree({cardStyle, cardSvg, borderColor}){
    return (
        <>
                <div key="arrange-module-3"className='flex justify-center h-36 w-36 rounded-[20px] border divination-arrange-component place-items-center' style={borderColor}>
                    <ThreeCardStyle uniqueKey="arrange-module3-1" cardStyle={cardStyle} cardSvg={cardSvg}/>
                    <ThreeCardStyle uniqueKey="arrange-module3-2" cardStyle={cardStyle} cardSvg={cardSvg}/>
                    <ThreeCardStyle uniqueKey="arrange-module3-3" cardStyle={cardStyle} cardSvg={cardSvg}/>
                </div>
        </>
    )
}

function ModuleCross({cardStyle, cardSvg, borderColor}){
    return (
        <>
                    <div key="arrange-module-5" className='grid grid-rows-3 h-36 w-36 rounded-[20px] border divination-arrange-component place-items-center' style={borderColor}>
                    <div>
                        <CrossCardStyle uniqueKey="arrange-module5-1" cardStyle={cardStyle} cardSvg={cardSvg}/>
                    </div>
                    <div className='flex gap-2'>
                        <CrossCardStyle uniqueKey="arrange-module5-2" cardStyle={cardStyle} cardSvg={cardSvg}/>
                        <CrossCardStyle uniqueKey="arrange-module5-3" cardStyle={cardStyle} cardSvg={cardSvg}/>
                        <CrossCardStyle uniqueKey="arrange-module5-4" cardStyle={cardStyle} cardSvg={cardSvg}/>
                    </div>
                    <div>
                    <CrossCardStyle uniqueKey="arrange-module5-5" cardStyle={cardStyle} cardSvg={cardSvg}/>
                    </div>

                
            </div>

        </>
    )
}

function HandleRunes({counts, cardStyle, cardSvg, borderColor}){

    switch(counts) {

        case 1:
            return <ModuleOne cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>
        
        case 3:
            return <ModuleThree cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>
        
        case 5:
            return <ModuleCross cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>

        default:
            console.log("No Module not found.")

    }

}

function HandleTarot({counts, cardStyle, cardSvg, borderColor}){
    
    switch(counts) {

        case 1:
            return <ModuleOne cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>
        
        case 3:
            return <ModuleThree cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>
        
        case 5:
            return <ModuleCross cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>
        
        default:
            console.log("No Module not found.")

    }

}

function HandleLenormand({counts, cardStyle, cardSvg, borderColor}){

    switch(counts) {

        case 1:
            return <ModuleOne cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>
        
        case 2:
            return <ModuleTwo cardStyle={cardStyle} cardSvg={cardSvg} borderColor={borderColor}/>

        default:
            console.log("No Module not found.")

    }

}

function HandleModule({method, counts}){

    const selectedStyle = CARD_STYLE_SETTINGS[method] || CARD_STYLE_SETTINGS.default;
    const borderColor = selectedStyle.arrangeBorderColor;

    switch(method) {

        case "runes":
            return <HandleRunes counts={counts} cardStyle={'divination-rune-card'} cardSvg={selectedStyle.cardSvg} borderColor={borderColor} />
        
        case "tarot":
            return <HandleTarot counts={counts} cardStyle={'divination-card'} cardSvg={selectedStyle.cardSvg} borderColor={borderColor}/>
        
        case "lenormand":
            return <HandleLenormand counts={counts} cardStyle={'divination-card'} cardSvg={selectedStyle.cardSvg} borderColor={borderColor}/>
        
        // case "whispers":
        //     return <ModuleTwo counts={counts} cardStyle={'divination-card'}/>
        
        // case "wisdom":
        //     return <ModuleTwo counts={counts} cardStyle={'divination-card'}/>

        default:
            console.log("No Module not found.")

    }
}



export default HandleModule;