import React, { useState, useEffect } from 'react';

import Introduction from './Introduction';
import Meditation from './Meditation';
import Arrangement from './Arrangement';
import ChooseCards from './ChooseCards';
import Prediction from './Prediction';

    
// This .js file for all cards methods.
function CardsService({ method, selectedStyle }){
    const [stage, setStage] = useState('');
    const [arrangeType, setArrangeType] = useState('');
    const [chosenCardCounts, setchosenCardCounts] = useState(0);

    const setStageFunc = (value) => {
        setStage(value);
        const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
        localData.state = value;
        localStorage.setItem('userPathMonitor', JSON.stringify(localData));
    };

    // TODO
    const setArrangeTypeFunc = (value) => {
        setArrangeType(value);
        const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
        localData.arrangeType = value;
        localStorage.setItem('userPathMonitor', JSON.stringify(localData));
    };

    // TODO
    const setCardCounts = (value) => {
        setchosenCardCounts(value);
        const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
        localData.cardCounts = value;
        localStorage.setItem('userPathMonitor', JSON.stringify(localData));
    }

    useEffect(() => {
        const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
        if (localData){
            if (localData.state){
                setStageFunc(localData.state)
            }
            if (localData.arrangeType){
                setArrangeType(localData.arrangeType)
            }
            if (localData.cardCounts){
                setCardCounts(localData.cardCounts)
            }
        }
      }, []);

    // Switch Stage based on stage
    switch(stage) {
        case "":
            // 
            // Tap
            // 
            return <Introduction method={method} setStageFunc={setStageFunc} selectedStyle={selectedStyle} />
    
        case "meditation":
        // 
        // 
        // +
            return <Meditation setStageFunc={setStageFunc} selectedStyle={selectedStyle} />

        case "arrangement":
            // 
            // 
            // 
            if (method === "whispers" || method === "wisdom") {
                const random = Math.random();
                const randomInteger = random < 0.5 ? 1 : 2;
                setCardCounts(randomInteger)
                setStageFunc("choose_cards")
                return <ChooseCards setStageFunc={setStageFunc} chosenCardCounts={chosenCardCounts} selectedStyle={selectedStyle} />
            } else {
                return <Arrangement method={method} setStageFunc={setStageFunc} setCardCounts={setCardCounts} setArrangeTypeFunc = {setArrangeTypeFunc} selectedStyle={selectedStyle} />
            }

        case "choose_cards":
        // 
        // 
        // method
            return <ChooseCards setStageFunc={setStageFunc} chosenCardCounts={chosenCardCounts} selectedStyle={selectedStyle} />
    
        case "explanation":
        // 
        // 
        // 
            return <Prediction method={method} arrangeType={arrangeType} chosenCardCounts={chosenCardCounts}/>

    }
}


export default CardsService;