import { useTranslation } from 'react-i18next';

import Navbar from './Common'
import React, { useRef, useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHatWizard } from '@fortawesome/free-solid-svg-icons'


function Language (){

}


function Header(){

  const { t } = useTranslation();

  return (
    <div className='grid place-items-center mb-6'>
    <div className="h-24 w-24 rounded-full border golden-border-color settings-profile-bg mb-4">
      {/* Photo */}
    </div>
    <p className='settings-golden-font-color text-sm'>Client22456</p>
    <div className='rounded-[20px] border golden-border-color deeper-blue-color py-1 px-3 mt-3'>
      {/* Original font color: golden-font-color  */}
      <p className='font-bold text-sm text-stone-500'>{t('settings.account')}</p>
      </div>
  </div>
  )
}


function LanguageSettings({setState}){

  const { t } = useTranslation();
  const [isBack, setIsback] = useState(false);

  const changeLanguage = (value) => {
    const userLanguage = JSON.parse(localStorage.getItem('userLanguage')) || null;
    if (!(userLanguage===value)){
      localStorage.setItem('userLanguage', JSON.stringify(value));
      window.location.reload();
    }
  };

  const turnBack = () => {
    const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
    localData.state = "";
    localStorage.setItem('userPathMonitor', JSON.stringify(localData));
    setIsback(true);
    setTimeout(() => {
      setState("")
    }, 1000);

  };

  return (
    <>
    <div className='grid place-items-center'>
    <button onClick={() => changeLanguage("en")}>
      <div className={`relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2 ${isBack && 'transition-all delay-100 ease-linear duration-500 translate-x-8 opacity-10'}`}>
      <div className="absolute right-0 -top-12 rotate-12"><FontAwesomeIcon icon={faHatWizard} size="4x" style={{color: "#c5a4e4",}} /></div>
        <p className='text-lg text-white'>English</p>
      </div>
      </button>
      <button onClick={() => changeLanguage("zh-TW")}>
      <div className={`relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2 ${isBack && 'transition-all delay-300 ease-linear duration-500 translate-x-8 opacity-10'}`}>
        <p className='text-lg text-white'></p>
      </div>
      </button>
      <button onClick={turnBack}>
      <div className={`relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2 ${isBack && 'transition-all delay-500 ease-linear duration-500 translate-x-8 opacity-10'}`}>
        <p className='text-lg text-white'>{t('settings.back')}</p>
      </div>
      </button>
  </div>
    </>
  )
}

function ResetSettings({setState}){

  const { t } = useTranslation();
  const [isBack, setIsback] = useState(false);
  const [isReset, setIsReset] = useState(false); 

  const resetLocalStorage = () => {
    // localStorage.removeItem('useLanguage');
    localStorage.removeItem('systemData');
    setIsReset(true);
  };

  const turnBack = () => {
    const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
    localData.state = "";
    localStorage.setItem('userPathMonitor', JSON.stringify(localData));
    setIsback(true);
    setTimeout(() => {
      setState("")
    }, 1000);

  };

  return (
    <>
    <div className='grid place-items-center'>
    <div className={`w-80 relative rounded-[20px] border border-2 settings-grid py-2 px-6 ${isBack && 'transition-all delay-300 ease-linear duration-100 translate-x-8 opacity-10'}`}>
      <div className="absolute right-3 -top-7 rotate-12"><FontAwesomeIcon icon={faHatWizard} size="4x" style={{color: "#c5a4e4",}} /></div>
        <p className='text-lg font-bold text-center about-title-font-color mb-2'>{t('settings.reset')}</p>
        <p className='text-base text-white mb-4'>{t('settings.resetMessage')}</p>
      </div>
      { isReset ?
        (
          <>
          <div className={`relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2 ${isBack && 'transition-all delay-300 ease-linear duration-500 translate-x-8 opacity-10'}`}>
            <p className='text-lg text-stone-600'>{t('settings.resetDone')}</p>
          </div>      
          </>
        ) : (
          <>
      <button onClick={resetLocalStorage}>
      <div className={`relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2 ${isBack && 'transition-all delay-300 ease-linear duration-500 translate-x-8 opacity-10'}`}>
        <p className='text-lg text-white'>{t('settings.reset')}</p>
      </div>
      </button>
          </>
        )
      }
      <button onClick={turnBack}>
      <div className={`relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2 ${isBack && 'transition-all delay-500 ease-linear duration-500 translate-x-8 opacity-10'}`}>
        <p className='text-lg text-white'>{t('settings.back')}</p>
      </div>
      </button>
  </div>
    </>
  )
}

function MainSettings({setState}){

  const { t } = useTranslation();

  const changeStage = (value) => {
    setState(value);
    const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
    localData.state = value;
    localStorage.setItem('userPathMonitor', JSON.stringify(localData));
  };

  return (
    <>
    <div className='grid place-items-center'>
      <div className='relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2'>
        <div className="absolute right-0 -top-12 rotate-12"><FontAwesomeIcon icon={faHatWizard} size="4x" style={{color: "#c5a4e4",}} /></div>
        <button onClick={() => changeStage("language")}>
        <p className='text-lg text-white'>{t('settings.language')}</p>
        </button>
      </div>
      <div className='h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2'>
        {/* relative text-white */}
        <p className='text-lg text-stone-600'>{t('settings.preference')}</p>
      </div>
      <div className='h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2'>
        {/* relative text-white */}
        <p className='text-lg text-stone-600'>{t('settings.version')}</p>
      </div>
      <div className='relative h-16 w-80 grid place-items-center rounded-[20px] border border-2 settings-grid py-4 px-2 mt-2'>
      <button onClick={() => changeStage("reset")}>
        <p className='text-lg text-white'>{t('settings.reset')}</p>
        </button>
      </div>
  </div>
    </>
  )
}

function Options(){

  const [state, setState] = useState('')

  useEffect(() => {
    const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
    if (localData){
        if (localData.state){
          setState(localData.state)
        }
    }
  }, []);
    
  switch(state) {

      case "":
          return <MainSettings setState={setState} />
      
      case "language":
          return <LanguageSettings setState={setState} />

      case "reset":
        return <ResetSettings setState={setState} />
      
      default:
        console.log("No Settings found!")
        return <MainSettings />
  }
}




function Settings({ setRouteFunc }){
  const { t } = useTranslation();

  return  (
    <>
    <div className={`wallpaper wallpaper-color ${t('lang') === 'en' ? 'font-acme' : ''}`}>
      <div className='px-6 pt-8'>
        <Header />
        <Options />
        {/* {user ? <ChatRoom /> : <SignIn />} <Options /> */}
      </div>
      <Navbar setRouteFunc={setRouteFunc}/>
    </div>
    </>
  )
  }

  export default Settings;