import { useTranslation } from 'react-i18next';
import lenormandCard from '../../../static/cards/ai/lenormand.jpg'
import whispersCard from '../../../static/cards/ai/whispers.jpg'
import wisdomCard from '../../../static/cards/ai/wisdom.jpg'

const tarotCards = require.context('../../../static/cards/tarot', true);
// const lenormandCards = require.context('../../../static/cards/lenormand', true);
// const whisperCards = require.context('../../../static/cards/whisper', true);
// const wisdomCards = require.context('../../../static/cards/wisdom', true);

function RunePhoto ({message}){

    switch(message.code) {
        default:
            return (
                <div className='h-72 w-[12rem] grid place-items-center rounded-[50px] divination-rune-result-card bg-contain bg-center drop-shadow-lg'><p className='text-8xl font-bold'>{message.photo}</p></div>
            )
        
        case "2":
            return (
                <div className='h-72 w-[12rem] grid place-items-center rounded-[50px] divination-rune-result-card bg-contain bg-center drop-shadow-lg'><p className='text-8xl font-bold rotate-180'>{message.photo}</p></div>
            )


    }
}

function TarotPhoto({ positionId, cardId }){

    const photo = tarotCards(`./${cardId}.jpg`);
    return (
        <div className='h-72 w-[12rem] rounded-[5px] border-2 divination-card bg-contain bg-center' style={{
            backgroundImage: `url(${photo})`, 
            backgroundSize: 'cover', // 
            backgroundRepeat: 'no-repeat', // 
            width: '100%', // 
            transform: positionId === '2' ? 'rotate(180deg)' : 'none', 
    }} />
    )
}

function LenormandPhoto({ cardId }){
    const { t } = useTranslation();
    // const photo = lenormandCards(`./${cardId}.JPG`);
    return (
        <>
        <div className='h-64 w-[12rem] rounded-[5px] border-2 divination-card' style={{
            backgroundImage: `url("${lenormandCard}")`,
            backgroundSize: 'cover', // 
            backgroundRepeat: 'no-repeat', // 
            width: '100%', // 
        }}></div>
        <p className="text-xs text-center italic mt-1">{t('divination.prediction.copyright')}</p>
        </>
    )
}

