function PhotoRender({method, cardId}){
    
    switch(method) {
        case 'whispers':
            return <WhisperPhoto cardId={cardId} />
        
        case 'wisdom':
            return <WisdomPhoto cardId={cardId} />

    }
}


export { RunePhoto, TarotPhoto, LenormandPhoto, PhotoRender };