import RunesPrediction from './prediction/Runes'
import TarotPrediction from './prediction/Tarot'
import LenormandPrediction from './prediction/Lenormand'
import { WhisperPrediction, WisdomPrediction} from './prediction/General'


function Prediction({method, arrangeType, chosenCardCounts}){

    // Switch Stage based on method
    switch(method) {
        case "runes":
            return <RunesPrediction method={method} arrangeType={arrangeType} chosenCardCounts={chosenCardCounts} />
        
        case "tarot":
            return <TarotPrediction method={method} arrangeType={arrangeType} chosenCardCounts={chosenCardCounts} />

        case "lenormand":
            return <LenormandPrediction method={method} arrangeType={arrangeType} chosenCardCounts={chosenCardCounts} />
        
        case "whispers":
            return <WhisperPrediction method={method} arrangeType={arrangeType} chosenCardCounts={chosenCardCounts} />

        case "wisdom":
            return <WisdomPrediction method={method} arrangeType={arrangeType} chosenCardCounts={chosenCardCounts} />

    }
}


export default Prediction;