import React, { useState, useEffect, useRef } from 'react';

import { MiriMessages } from '../../Miri';
import { SingleExplanation, fetchData, LoadingPage } from '../../Component';
import { OneSizeResultCard, TwoSizeResultCard, MessageCardExplanation } from './Component';
import { LenormandPhoto } from './Photos'


function ModuleOne({method, chosenCardCounts}){

    const initMessage = <MessageCardExplanation/>

    const [selectedDiv, setSelectedDiv] = useState(null);

    const [message, setMessage] = useState(initMessage);
    const [singleMessage, setSingleMessage] = useState({});
    const divRef = useRef(null);

    const [chosenData, setChosenData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    let body = {
        "method": method,
        "action": "prediction",
        // TODO need arrangeType?
        "counts": chosenCardCounts
    }

    useEffect(() => {
        //  API
        const detectData = async () => {
        try{
            const localData = JSON.parse(localStorage.getItem('userPathMonitor')) || {};
            
            if (localData.chosenData) {
                setChosenData(localData.chosenData);
                setIsLoading(false);
            } else {
                const data = await fetchData(body);
                localData.chosenData = data;

                localStorage.setItem('userPathMonitor', JSON.stringify(localData));
                
                setChosenData(data);
                setIsLoading(false);

            }
          } catch (error) {
            console.error(error)
          }
        };
        detectData();
      }, []);

    const handleDivClick = (divId) => {
        if (selectedDiv == null) {
            setSelectedDiv(true);
            const singleExplain = chosenData.cards.find(item => item.id === divId);
            // console.log(singleExplain)
            setSingleMessage(singleExplain)
            setMessage(divId);
        } else {
            setSelectedDiv(null);
            setMessage(initMessage);
        }
        
      };

    const handleClickOutside = (event) => {
    if (divRef.current && !divRef.current.contains(event.target)) {
        setSelectedDiv(null);
        setMessage(initMessage);
    }
    };

    useEffect(() => {
        document.addEventListener('click', handleClickOutside);
        return () => {
          document.removeEventListener('click', handleClickOutside);
        };
      }, []);

    const extraClassName = selectedDiv ? 'blur-sm' : '';

    return (
        <>
        {isLoading ? (
            <LoadingPage/>

        ) : (
            <div>
            {selectedDiv && <SingleExplanation message={singleMessage} photo={<LenormandPhoto cardId={singleMessage.id} />} method={method} closeFunc={() => handleDivClick(singleMessage.id)} />}
            <div className={`pt-28 px-8 ${extraClassName}`}>
            <div ref={divRef} className='relative h-[26rem] p-2 grid rounded-[20px] border divination-result-section place-content-center'>
                    <div/>
                    <div/>
                    <div/>
                    <div/>
                    {chosenData.cards.map((data) => (
                        <div key = {`div${data.id}`} onClick={() => handleDivClick(data.id)}>
                            <OneSizeResultCard card={ data }/>
                        </div>
                    ))}
                    <div/>
                    <div/>
                    <div/>
                    <div/>
                </div>
                </div>
            </div>
        )}
        <div className={`fixed inset-x-0 bottom-0 ${extraClassName}`}>
        <MiriMessages message={message} buttons={null} hideEffect={false} settings={'explanation'}/>
        </div>
        </>
    )

}

